body {
  background-color: none;
  font-size: 30px;
}
div.box {
  background: #f00;
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}
table {
  width: 500;
  height: 400;
  background: #800080;
  border: 1px solid #f00;
  border-radius: 20px;
  color: #f00;
}
table td {
  color: #f00;
  border: 1px solid #000;
  border-radius: 20px;
}
